import React, { useState } from "react";
import { navigate } from "gatsby-link";

import { FaEnvelope, FaUser } from "react-icons/fa";

const encode = (data) => {
  return Object.keys(data)
    .map((key) => encodeURIComponent(key) + "=" + encodeURIComponent(data[key]))
    .join("&");
};

const ContactForm = () => {
  const [state, setState] = useState({});
  const subject = "Messaggio da modulo contatti sito www.fucinatalenti.it";

  const handleChange = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
      subject, // let overwrite subject in email notification
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const form = e.target;
    const formName = form.getAttribute("name");
    const data = {
      "form-name": formName,
      ...state,
    };

    fetch("/", {
      method: "POST",
      headers: { "Content-Type": "application/x-www-form-urlencoded" },
      body: encode(data),
    })
      .then(() => navigate(form.getAttribute("action")))
      .catch((error) => alert(error));
  };

  return (
    <div className="container is-max-desktop">
      <div className="columns">
        <div className="column">
          <form
            action="/thanks"
            data-netlify="true"
            method="POST"
            name="contact"
            netlify-honeypot="my-super-field"
            onSubmit={handleSubmit}
          >
            <p className="is-hidden">
              <input type="hidden" name="form-name" value="contact" />
            </p>
            <p className="is-hidden">
              <input
                type="hidden"
                name="subject"
                value="Messaggio da modulo contatti sito www.fucinatalenti.it"
              />
            </p>
            <p className="is-hidden">
              <label>
                Donâ€™t fill this out:
                <input name="my-super-field" onChange={handleChange} />
              </label>
            </p>

            <div className="columns">
              <div className="column">
                <div className="field">
                  <label htmlFor="name" className="label">
                    Nome
                  </label>
                  <div className="control has-icons-left">
                    <input
                      id="name"
                      name="name"
                      className="input"
                      type="text"
                      placeholder="Scrivi Nome"
                      onChange={handleChange}
                    />
                    <span className="icon is-small is-left">
                      <FaUser />
                    </span>
                  </div>
                </div>
                <div className="field">
                  <label htmlFor="email" className="label">
                    Email
                  </label>
                  <div className="control has-icons-left">
                    <input
                      id="email"
                      name="email"
                      className="input"
                      type="email"
                      placeholder="Email"
                      onChange={handleChange}
                    />
                    <span className="icon is-small is-left">
                      <FaEnvelope />
                    </span>
                  </div>
                </div>
              </div>

              <div className="column">
                <div className="field">
                  <label htmlFor="message" className="label">
                    Message
                  </label>
                  <div className="control">
                    <textarea
                      id="message"
                      name="messaggio"
                      className="textarea"
                      placeholder="Il tuo testo"
                      onChange={handleChange}
                    ></textarea>
                  </div>
                </div>

                <div className="field">
                  <div className="control">
                    <label className="checkbox">
                      <input
                        type="checkbox"
                        name="consenso-privacy"
                        required
                        className="mr-3"
                        value="yes"
                        onChange={handleChange}
                      />
                      Acconsento al trattamento dei dati.{" "}
                      <a href="/privacy">Approfondisci</a>
                    </label>
                  </div>
                </div>

                <div className="field is-grouped">
                  <div className="control">
                    <button type="submit" className="button is-link">
                      Submit
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default ContactForm;
